/* Reset & Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background-color: #f5f7fa;
  color: #222;
  line-height: 1.6;
}

/* App Container */
.App {
  max-width: 1080px;
  margin: 0 auto;
  padding: 24px;
}

/* Section Styles */
section {
  margin: 64px 0;
}

/* Hero Section */
.hero-section {
  background: linear-gradient(135deg, #0f2027, #203a43, #2c5364);
  padding: 100px 20px;
  text-align: center;
  color: #fff;
  border-radius: 12px;
}

.hero-logo {
  margin-bottom: 24px;
}

.logo {
  width: 200px;
  height: auto;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.hero-section h1 {
  font-size: 3rem;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

.hero-section p {
  font-size: 1.25rem;
  font-weight: 300;
  opacity: 0.9;
}

/* About Section */
.about {
  background: #fff;
  padding: 48px 32px;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
}

.about h2 {
  font-size: 2rem;
  color: #111;
  margin-bottom: 20px;
}

.about p {
  font-size: 1.1rem;
  color: #444;
  line-height: 1.7;
}

/* Projects Section */
.projects {
  padding: 48px 20px;
}

.projects h2 {
  text-align: center;
  font-size: 2rem;
  color: #111;
  margin-bottom: 32px;
}

.project-item {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.1);
  padding: 24px;
  margin-bottom: 28px;
  transition: transform 0.3s ease;
}

.project-item:hover {
  transform: translateY(-6px);
}

.project-item h3 {
  font-size: 1.4rem;
  color: #222;
  margin-bottom: 12px;
}

.project-item p {
  font-size: 1rem;
  color: #555;
}

/* Contact Section */
.contact {
  background: #fff;
  padding: 48px 32px;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
  text-align: center;
}

.contact h2 {
  font-size: 2rem;
  color: #111;
  margin-bottom: 24px;
}

.contact p {
  font-size: 1rem;
  color: #444;
  margin-bottom: 12px;
  word-break: break-word;
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-section h1 {
    font-size: 2.2rem;
  }

  .App {
    padding: 16px;
  }

  .about,
  .contact {
    padding: 32px 20px;
  }
}
